<template>
  <div class="header-wrapper">
    <h3 class="title bold-600">
      <!-- <button class="back-btn" @click="$router.go(-1)"><fa icon="angle-left"></fa></button> -->
      {{ $t('header.' + this.$route.name) }}
    </h3>
    <!-- <LanguageSwitcher /> -->
      
    <!-- <div class="navbar-right">
      <div class="store-selection-wrapper">
        <select v-model="selectedStore" @change="changeStore">
          <option v-for="company in companies" :key="company" :value="company.id">
            {{ company.companyName }}
          </option>
        </select>
        <span class="store-icon"><fa icon="store"></fa></span>
      </div>
      <button><fa :icon="['far', 'bell']"></fa></button>
      <button><fa :icon="['fas', 'table-cells-large']"></fa></button>
      <button @click="hideUserMenu = !hideUserMenu"><fa :icon="['far', 'user']"></fa></button>
    </div>
    <Transition name="fade">
      <ul v-if="hideUserMenu" class="user-menu">
        <li><a @click="logUserIn()"><fa icon="sign-in" /> Sign In</a></li>
        <li><a @click="accessToken()"><fa icon="key" /> Access Token</a></li>
        <li><a @click="userIdentity()"><fa icon="store" /> Get Store</a></li>
        <li><a @click="logUserOut()"><fa icon="sign-out" /> Sign Out</a></li>
      </ul>
    </Transition> -->
  </div>
</template>

<script>
/* eslint-disable */
import LanguageSwitcher from "@/components/LanguageSwitcher.vue";
import i18n from '../i18n'
import Auth from "@/services/MQCloudAuthService.js";
import Oidc from "oidc-client";

export default {
  name: "item-header",
  components: {
    LanguageSwitcher
  },
  setup() {
    const { t } = i18n.global

    return {
      t
    }
  },
  data() {
    return {
      auth: new Auth(),
      selectedStore: 1027,
      // companies: [{
      //   id: 1027,
      //   name: "J Mart(NY-Brooklyn)"
      // },{
      //   id: 1221,
      //   name: "J Mart(Little Neck)"
      // }],
      companies:[],
      hideUserMenu: false,
    };
  },
  created() {
    new Oidc.UserManager({ response_mode: "query" })
      .signinRedirectCallback()
      .then(function () {
        window.location.href = "/bkoffice-reports";
        // window.location.href = "/";
      })
      .catch(function (e) {});
  },
  mounted() {
    if (localStorage.getItem("StoreID")) {
      this.selectedStore = localStorage.getItem("StoreID");
    } else {
      localStorage.setItem("StoreID", 1027);
      localStorage.setItem("StoreName", "J Mart(NY-Brooklyn)");
      this.selectedStore = localStorage.getItem("StoreID");
    }

    const urlParams = new URLSearchParams(window.location.search);
    const codeParams = urlParams.get("code");

    if (codeParams) {
      new Oidc.UserManager({ response_mode: "query" })
        .signinRedirectCallback()
        .then(function () {
          window.location.href = "/bkoffice-reports";
          // window.location.href = "/";
        })
        .catch(function (e) {});
    } else {
      let self = this;

      this.auth.userIdentity().then(
        (result) => {
          this.companies = result.companies;

          localStorage.setItem("companies", JSON.stringify(this.companies));
          
          if (localStorage.getItem("StoreID")) {
            this.selectedStore = localStorage.getItem("StoreID");
          } else {
            localStorage.setItem("StoreID", result.companies[0].id);
            this.selectedStore = localStorage.getItem("StoreID");
          }

          for (let i = 0; i < this.companies.length; i++) {
            if (this.companies[i].id == this.selectedStore) {
              localStorage.setItem("StoreName", this.companies[i].companyName);
            }
          }
        },
        (err) => {
          this.auth.login();
          console.log(err);
        }
      );
    }
  },
  methods: {
    changeStore() {
      localStorage.setItem("StoreID", this.selectedStore);

      for (let i = 0; i < this.companies.length; i++) {
        if (this.companies[i].id == this.selectedStore) {
          localStorage.setItem("StoreName", this.companies[i].name);
        }
      }

      window.location.reload();
    },
    logUserIn() {
      this.auth.login();
    },
    accessToken() {
      let self = this;
      this.auth.accessToken().then(
        (result) => {
          this.loginAccessToken = result;
          localStorage.setItem("LoginAccessToken", this.loginAccessToken);
        },
        (err) => {
          console.log(err);
        }
      );
    },
    userIdentity() {
      let self = this;
      this.auth.userIdentity().then(
        (result) => {
          this.companies = result.companies;
          this.selectedLocation = result.companies[0].id;
        },
        (err) => {
          console.log(err);
          this.auth.login();
        }
      );
    },
    logUserOut() {
      localStorage.removeItem("LoginAccessToken");
      this.auth.logout();
    },
  },
};
</script>

<style scoped>
.header-wrapper {
  height: 50px;
  position: relative;
  display: flex;
  justify-content: space-between;
  margin-bottom: 5px;
  /* padding: 10px 0px; */
  background-color: #fff;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  z-index: 3;
}
.header-wrapper .navbar-right {
  margin: auto 10px;
  display: flex;
  align-items: center;
}
.header-wrapper .navbar-right button {
  width: 30px;
  height: 30px;
  color: #777777;
  font-size: 18px;
  border: none;
  border-radius: 50%;
  margin-right: 10px;
  transition: 0.3s;
  background-color: #fff;
}
.store-selection-wrapper {
  position: relative;
}
select {
  margin-right: 10px;
  padding: 10px 30px 10px 35px;
  border: 1px solid #e8e8e8;
  border-radius: 5px;
  transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
}
select:hover {
  border-color: #aaaeb7;
}
.store-icon {
  position: absolute;
  left: 0;
  height: 100%;
  font-size: 18px;
  align-content: center;
  color: #959595;
  padding-left: 10px;
}
.user-menu {
  position: absolute;
  right: 25px;
  top: 45px;
  background-color: #fff;
  padding: 10px;
  border-radius: 10px;
  border: 1px solid #e8e8e8;
  z-index: 1;
}
.user-menu li {
  padding: 10px;
  border-radius: 5px;
  transition: 0.3s;
}
.user-menu li svg {
  width: 20px;
}
.header-wrapper .navbar-right button:hover,
.user-menu li:hover {
  background: rgba(0, 0, 0, 0.07);
}
@media only screen and (max-width: 1550px) {
  select {
    margin-right: 50px;
  }
}
@media only screen and (max-width: 710px) {
  /* .header-wrapper {
    display: block;
  } */
  .header-wrapper .navbar-right {
    justify-content: flex-end;
    margin-top: 15px;
  }
  select {
    margin-right: 10px;
  }
}
</style>
