<template>
  <div class="lang-wrapper">
    <button v-if="locale == 'en'" class="lang-btn" @click="setLanguage('ch')">EN</button>
    <button v-if="locale == 'ch'" class="lang-btn" @click="setLanguage('en')">中</button>
  </div>
</template>

<script>
import i18n from '../i18n'
import { watchEffect } from 'vue';

export default {
 setup() {
  const { locale } = i18n.global;

  const setLanguage = (newLang) => {
    locale.value = newLang;
    localStorage.setItem('lang', newLang);
  };

  watchEffect(() => {
    const storedLang = localStorage.getItem('lang');
    if (storedLang) {
      locale.value = storedLang;
    }
  });

   return {
    locale,
    setLanguage
  }
 }
}
</script>

<style>
.lang-btn {
  position: absolute;
  right: 10px;
  top: 10px;
  height: 30px;
  width: 32px;
  background-color: #000;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 0;
  opacity: 1;
  z-index: 10;
  transition: .5s ease-in-out;
}
.lang-btn:hover {
  opacity: 0.7;
}
</style>
